/* top | right | bottom | left */ 
/* padding default: 20px 30px*/
.post-excerpt {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin: 30px 0;
  padding: 12px 35px;
  border-radius: var(--rounded);
  background: var(--background);
  text-align: left;
  box-shadow: var(--shadow);
}
 
.post-excerpt > * {
  margin: 0 !important;
}

.post-excerpt > a:first-child {
  width: 100%;
  font-weight: var(--semi-bold);
  font-size: large;
}

.post-excerpt > div {
  justify-content: flex-start;
}

.post-excerpt > p {
  width: 80%;
}
